.skills-section {
    display: flex;
    flex-direction: column;
    padding-top: 50px;
    padding-bottom: 50px;

    background-color: #F5F5F5;

    .title {
        margin-left: 4vw;
        font-size: 40px;
        color: #000000;
    }

    .skills-container {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        flex-wrap: wrap;
    }

    .skill-element {
        display: flex;
        justify-content: center;
        align-items: center;

        width: 20vw;
        height: 30vh;
        margin-bottom: 40px;
        border-radius: 20px;
        background-color: #FFFFFF;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

        img {
            width: 15vw;
            height: 20vh;
        }
    }
}